import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {FetchService} from '../fetch.service';


@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {

  Articles=[];
  tag={
    tagName:""
  };
  suggestedTags="";
  time="";
  sTag={
    tagName:""
  };

  constructor(private _fetch  : FetchService,
              private _router : Router) {
  }

  ngOnInit() {
  }

  search(){
      localStorage.setItem('name', this.tag.tagName);
      this._fetch.fetchArticle(this.tag)
      .subscribe(
        res=>{
           this.Articles=res[0];
           this.suggestedTags=res[1];
           this.time=res[2];
           console.log(this.Articles);
           console.log(this.suggestedTags);

          // this._router.navigate(['/article',res]);
        },err=>{
          console.log('error in routing to article')
        }
      )
  }
  newSearch(taggg){

    localStorage.setItem('name', taggg);
    this._fetch.fetchArticle({tagName:taggg})
    .subscribe(
      res=>{
         this.Articles=res[0];
         this.suggestedTags=res[1];
         this.time=res[2];
         console.log(this.Articles);
         console.log(this.suggestedTags);

        // this._router.navigate(['/article',res]);
      },err=>{
        console.log('error in routing to article')
      }
    )
  }

}
